import React, { useContext, useEffect, useState } from 'react';

import { StoreContext } from '../store/store';

import ParticlesBackground from './ParticlesBackground';
import { Player, SpikeLeft, SpikeRight, SpikesTop } from './Shapes';


/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const Board = () => {

    const [ side, setSide ] = useState(false);
    const changeSide = () => setSide(prev => !prev);

    const [ addNewSpike, setAddNewSpike ] = useState(true);
    const [ inGameSpikes, setInGameSpikes ] = useState([]);
    const [ score, setScore ] = useState(0);

    /*   *   *   *   *   *   *   *   *   *   */

    const [ stop, setStop ] = useState(true);
    const toggleStop = () => setStop(prev => !prev);

    /*   *   *   *   *   *   *   *   *   *   */

    const { player } = useContext(StoreContext);
    const { spikes } = useContext(StoreContext);
    const { setSpikes } = useContext(StoreContext);

    /*   *   *   *   *   *   *   *   *   *   */

    const addRandSpike = () => {
        const newSpike = Boolean(Math.floor(Math.random()*2))
            ? <SpikeLeft key={score}/>
            : <SpikeRight key={score}/>;

            
        setInGameSpikes(prev => [ newSpike, ...prev.slice(0, 19) ]);
        setScore(prev => prev + 1)
    }

    /*   *   *   *   *   *   *   *   *   *   */

    useEffect(() => {

        if(stop) {
            setAddNewSpike(false);
            setInGameSpikes([]);
            setScore(0);

            setSpikes([]);
        }

        if(!stop) {
            setTimeout(() => addRandSpike(), 500);
            setTimeout(() => setAddNewSpike(prev => !prev), 1000 * 100 / (score + 100));
        }

    }, [addNewSpike, stop]);

    /*   *   *   *   *   *   *   *   *   *   */
    
    return(
    <section className='board' onClick={!stop ? changeSide : null}>


        <ParticlesBackground speed={!stop ? 6 : 1}/>
        <SpikesTop/>


        <button className='start-stop' onClick={toggleStop}>[button]</button>


        <Player idName='player' side={side}/>


        {inGameSpikes}


    </section>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default Board;